import React, { useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';


import { advanceConvo, selectScript, selectStep} from './convoScriptSlice3';


//import styles from './Counter.module.css';
import Card from 'react-bootstrap/Card';
import Button from 'react-bootstrap/Button';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Accordion from 'react-bootstrap/Accordion';
import Form from 'react-bootstrap/Form';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import { useLocation } from "react-router-dom";

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

export function ConvoScript3() {
    const script = useSelector(selectScript);
    const scriptStep = useSelector(selectStep);
    const dispatch = useDispatch();
    var value = script[scriptStep];
    const query = useQuery();
    //Variables
    const campaign = query.get("campaign")
    const leadId = query.get("lead_id")
    const listId = query.get("list_id")
    const gmtOffset = query.get("gmt_offset_now")
    const phone = query.get("phone_number")
    const altPhone = query.get("alt_phone")
    const firstName = query.get("first_name")
    const lastName = query.get("last_name")
    const address1 =  query.get("address1")
    const city1 = query.get("city")
    const state = query.get("state")
    const postalCode = query.get("postal_code")
    const ScheduledDayOfWeek = query.get("scheduledDayOfWeek")
    const scheduledate = query.get("scheduledDate")
    const meridiem = query.get("meridiem")
    const agentName = query.get("agentName")
    const suggestedProvider = query.get("suggestedProvider")
    const user = query.get("user")
    const ffType = query.get("ffType")
    const devMode = query.get("devMode")
    const ost = query.get("ost")
    const email = query.get("email")
    const address3 = query.get("address3")
    const svcReg = query.get("svcReg")
    const group = query.get("group")
    //var clientFN = "Jose Andres";
    //var address = "El address del cliente";
    //var city = "La ciudad del cliente"
    //console.log(script);
    //console.log(scriptStep);
    //console.log(value);
    const [clientFN, setClientFN] = useState(''); 
    const [clientLN, setClientLN] = useState(''); 
    const [address, setAddress] = useState('');
    const [city, setCity] = useState('');
    
    const clearClientDataFromForm = () => {
        setClientFN('');
        setClientLN('');
        setAddress('');
        setCity('');
        dispatch(advanceConvo('1'));
    }
 
    const nextDialog = (resp) => {
        console.log(resp);
        console.log(resp[0]);
        dispatch(advanceConvo(resp[1]));
        if (resp[0] == "Not Interested") {
            console.log("not interested if");
            
        }
        else if (resp[0] == "Set up an Appointment") {
            console.log("appointment if");
            
        }
        else if (resp[0] == "Still not interested") {
            console.log("still not interested if");
           
        }

        //console.log(totalScore);

    }
 
    return (
        <>
            <hr />
            <Row>
                <Accordion defaultActiveKey="0">
                    <Accordion.Item eventKey="0">
                        <Accordion.Header>Client Data</Accordion.Header>
                        <Accordion.Body>

                            <Form>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>First Name</Form.Label>
                                    <Form.Control placeholder="Disabled input" disabled value={firstName}  type="text"  />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Last Name</Form.Label>
                                    <Form.Control placeholder="Disabled input" disabled value={lastName} type="text"  />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>City</Form.Label>
                                    <Form.Control placeholder="Disabled input" disabled value={city1} type="text"  />
                                </Form.Group>
                                <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                                    <Form.Label>Address</Form.Label>
                                    <Form.Control placeholder="Disabled input" disabled value={address1} type="text"  />
                                </Form.Group>
                            </Form>
                            <div className="d-grid gap-2">
                                <Button variant="danger" size="lg" onClick={clearClientDataFromForm} > Reset Conversation and Clear Data</Button>
                            </div>
                        </Accordion.Body>
                    </Accordion.Item>
                </Accordion>
                <hr />
            </Row>
            <Row>
                <Card>
                    <Card.Header as="h5">{value[0]}</Card.Header>

                    <Card.Body>
                        <Card.Title> { }</Card.Title>
                        <Tabs id="uncontrolled-tab-example" className="mb-3">
                            {value[1].map((dialog, index) => <Tab key={index} eventKey={index + 1} title={'#' + (index + 1)}> {(value[1][index]).replace("--A--first_name--B--", clientFN).replace("--A--city--B--", city).replace("--A--address1--B--", address).replace("--A--user_name--B--")} </Tab>)}
                        </Tabs>

                    </Card.Body>
                </Card>
            </Row>
            <Row>
            <div className="mb-2 ">
                {value[2].map((replies, index) =>  <Button size="md" className={index === 0 ? 'm-2 btn-success' : (index === 1 ? 'm-2 btn-danger' : 'm-2 btn-warning')} onClick={() => nextDialog(replies)} > {replies[0]} </Button>)}
            </div>
            </Row>

            <hr />
            

        </>

    );
}