import React, {useState} from 'react'
import {ConvoScript3}  from '../features/convoScript/ConvoScript3'
import { Link, useNavigate } from "react-router-dom";
import {BrowserRouter as Router, Route, Routes} from 'react-router-dom';

import { Button } from 'react-bootstrap'
import { useLocation } from "react-router-dom";

function useQuery() {
    return new URLSearchParams(useLocation().search);
}

export default function Dashboard() {

    const [error, setError] = useState('');
    const {user, logout} = useState("")
    const navigate = useNavigate();
    const [scriptNum, setScriptNum] = useState('1');
    const query = useQuery();
    //Variables
    const campaign = query.get("campaign")
    const leadId = query.get("lead_id")
    const listId = query.get("list_id")
    const gmtOffset = query.get("gmt_offset_now")
    const phone = query.get("phone_number")
    const altPhone = query.get("alt_phone")
    const firstName = query.get("first_name")
    const lastName = query.get("last_name")
    const address1 =  query.get("address1")
    const city1 = query.get("city")
    const state = query.get("state")
    const postalCode = query.get("postal_code")
    const ScheduledDayOfWeek = query.get("scheduledDayOfWeek")
    const scheduledate = query.get("scheduledDate")
    const meridiem = query.get("meridiem")
    const agentName = query.get("agentName")
    const suggestedProvider = query.get("suggestedProvider")
    const user1 = query.get("user")
    const ffType = query.get("ffType")
    const devMode = query.get("devMode")
    const ost = query.get("ost")
    const email = query.get("email")
    const address3 = query.get("address3")
    const svcReg = query.get("svcReg")
    const group = query.get("group")
    const handleLogout = async () => {
        
        try {
            setError('');
            await logout();
            navigate("/login");
        } catch (error) {
            setError(error.message);
        }
    }

    const handleScript = async () => {
        
        try {
            setError('');
            navigate(`/?campaign=${campaign}
            &lead_id=${leadId}
            &list_id=${listId}
            &gmt_offset_now=${gmtOffset}
            &phone_number=${phone}
            &alt_phone=${altPhone}
            &first_name=${firstName}
            &last_name=${lastName}
            &address1=${address1}
            &city=${city1}
            &state=${state}
            &postal_code=${postalCode}
            &scheduledDayOfWeek=${ScheduledDayOfWeek}
            &scheduledDate=${scheduledate}
            &meridiem=${meridiem}
            &agentName=${agentName}
            &suggestedProvider=${suggestedProvider}
            &user=${user1}
            &ffType=${ffType}
            &devMode=${devMode}
            &ost=${ost}
            &email=${email}
            &address3=${address3}
            &svcReg=${svcReg}
            &group=${group}`);
        } catch (error) {
            setError(error.message);
        }
    }
    

    return (
        <div>
            
           

            {/*<Button className="w-100 my-3" variant="primary" onClick={handleScript}> 
                        Script 
            </Button>*/}

            <ConvoScript3/>
            
        </div>


    )
}
